.subscribe-form {
  display: flex;
  .btn{
    margin-left: 20px;
  }
}

form {
  input {
    height: 50px;

    &[type=submit] {
      font-size: 16px;
    }

    &[type=email],&[type=text] {
      padding: 10px 15px;
      border-radius: 25px;
      border: none;
      outline: none
    }
  }
}